import motal from '../../../element/motal.vue'; //引入登录模态框
import HMmessages from '@/common/components/HM-messages/HM-messages.vue';

var db = wx.cloud.database();
var users = db.collection('user');
var message = db.collection('message');
// 引入当前时间的js
var util = require('../../../common/util.js');
var time = util.formatTime(new Date());

export default {
	name: 'message',
	components: {
		motal,
		HMmessages
	},
	props:{
		messageWord:Array,
		leaveword:Array,
		dataId:String
	},
	data() {
		return {
			num: 0,
			box: false,
			Comment:'',//评价信息
			avatarUrl: '', //头像
			nickname: '', //昵称
			newMessageWord:[] ,//添加一个‘全部’分词
			id:''
		};
	},
	methods: {
		menubtn(index, item) {
			this.num = index;
			this.$parent.fatherMethod(item)
		},
		//显示评论框
		popup() {
			users
				.get()
				.then(res => {
					if (res.data.length == 0) {
						let message = '请登录后再评价';
						this.$nextTick(() => {
							this.$refs.mon.init(message);
						});
					} else {
						let userData = res.data[0];
						this.avatarUrl = userData.avatarUrl;
						this.nickname = userData.nickName;
						this.box = true;
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
		//提交评价
		btn() {
			if (this.Comment == "") {
				let tip = '评论不能为空';
				let icon = 'error';
				this.tips(tip, icon);
			}else {
				//调用云函数
				//1.先提交到百度做分析处理，返回数据 2.提交到数据库
				this.submit()
			}
		},
		async submit(){
			//调用云函数到百度云分析处理返回标签
			let stamess = await this.aiMessage()
			//console.log(stamess)
			let classif = ''
			if(stamess.length > 0){
				let ali = stamess[stamess.length - 1]
				let [prop,adj] = [ali.prop,ali.adj]//es6结构
				classif = prop + adj
			}
			//提交到数据库
			await this.messdata(classif)
		},
		//提交到数据库
		messdata(classif){
			return new Promise((resolve,reject) => {
				//把所有需要提交的数据以对象的形式提交
				var messArray = {
					usermess: this.Comment,//评价内容
					time: time,//提交的时间
					nickname: this.nickname,//用户昵称
					avatarUrl: this.avatarUrl//用户头像
				}
				message.add({
					data: {
						id: this.id,
						classMessage: classif, //分析词
						messageData: messArray //数据
					}
				}).then((res) => {
					let tip = '评论成功';
					let icon = 'success';
					this.tips(tip, icon);
					//清空输入框
					this.Comment = ''
					//评价成功刷新评价
					let className = '全部'
					this.$parent.fatherMethod(className)
					this.num = 0
					this.box = false
				}).catch((err) => {
					console.log(err)
				})
			})
		},
		//调用云函数到百度云分析处理返回标签
		aiMessage(){
			return new Promise((resolve,reject) => {
				wx.cloud.callFunction({
					name:'aimessage',
					data:{
						message:this.Comment
					}
				}).then((res) => {
					let aidata = res.result.aimessage.items
					resolve(aidata)
				}).catch((err) => {
					reject('出错了')
				})
			})
		},
		//点击取消，隐藏评论框
		messcancel() {
			this.box = false;
		},
		tips(tip, icon) {
			this.HMmessages.show(tip, {
				icon: icon,
				iconColor: '#ffffff',
				fontColor: '#ffffff',
				background: 'rgba(102,0,51,0.8)'
			});
		}
	},
		
	watch:{
		//添加一个“全部”分词
		messageWord(newValue,oldValue){
			this.newMessageWord = ['全部',...newValue]
		},
		dataId(newValue,oldValue){
			console.log(newValue);
			this.id = newValue;
		}
	}
};
